header {
  padding-top: 30px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 9;

  .map {
    margin-left: 7.5%;
    margin-right: 8.1%;

    img {
      margin-right: 15px;
    }

    .li {
      color: #fff;
      font-size: 15px;
      font-weight: 400;
      line-height: 22px;

      &:first-child {
        margin-bottom: 8px;
      }

      &:before {
        content: '';
        width: 4px;
        height: 4px;
        background-color: #4896ff;
        display: inline-block;
        border-radius: 100%;
        margin-right: 7px;
        margin-bottom: 2px;
      }
    }
  }

  .phone {
    color: #13151a;
    font-size: 26px;
    line-height: 28px;

    a {
      color: #fff;
      font-size: 24px;
    }
  }

  .online {
    color: rgba(255, 255, 255, .7);
    font-size: 15px;
    font-weight: 400;
    display: flex;
    align-items: center;
    text-align: right;
    justify-content: flex-end;
    margin-top: 5px;

    .pulse {
      width: 6px;
      height: 6px;
      background-color: #91d166;
      border-radius: 50%;
      left: 46px;
      -webkit-animation-iteration-count: infinite;
      animation-iteration-count: infinite;
      -webkit-animation-name: pulse;
      animation-name: pulse;
      -webkit-transform-origin: center center;
      -ms-transform-origin: center center;
      transform-origin: center center;
      -webkit-animation-duration: 1s;
      animation-duration: 1s;
      -webkit-font-smoothing: antialiased;
      opacity: 1;
      float: left;
      margin-right: 11px;
    }
  }

  .head-btn {
    width: 200px;
    height: 56px;
    border: 1px solid #ffffffb3;
    background: transparent;
    color: #ffffffb3;
    font-size: 12px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: normal;
    line-height: 22px;
    text-align: center;
    text-transform: uppercase;
    font-style: normal;
    letter-spacing: .44px;
    border-radius: 28px;
    margin-left: 20px;
  }
}